.dashboard {
  @media screen and (max-width: 960px) {
  .p-datatable {
    &.p-datatable-customers {
      .p-datatable-thead > tr > th,
      .p-datatable-tfoot > tr > td {
        display: none !important;
      }

      .p-datatable-tbody > tr {
        border-bottom: 1px solid #dee2e6;
        > td {
          text-align: left;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 0 none !important;
          width: 100% !important;
          float: left;
          clear: left;
          border: 0 none;

          .p-column-title {
            padding: .4rem;
            min-width: 30%;
            display: inline-block;
            margin: -.4rem 1rem -.4rem -.4rem;
            font-weight: bold;
          }

          .p-progressbar {
            margin-top: .5rem;
          }
        }
      }
    }
  }
}
}

.floatlabel-demo {
.p-field {
      margin-top: 2rem;
      margin-bottom: 0;
}
}

.input-demo {
  .p-multiselect {
  min-width: 15rem;
}

.multiselect-custom {
  .country-item {
    display: flex;
    align-items: center;

    span.flag {
      width: 18px;
      height: 12px;
      margin-right: .5rem;
      margin-left: .5rem;
    }
  }

  .country-item-value {
    border-radius: 3px;
    display: inline-flex;
    margin-right: .5rem;
    background-color: #2196F3;
          color: #ffffff;
  }
}
}

.list-demo {
  .product-name {
      font-size: 1.5rem;
      font-weight: 700;
  }

  .product-description {
      margin: 0 0 1rem 0;
  }

  .product-category-icon {
      vertical-align: middle;
      margin-right: .5rem;
  }

  .product-category {
      font-weight: 600;
      vertical-align: middle;
  }

  .product-list-item {
      display: flex;
      align-items: center;
      padding: 1rem;
      width: 100%;

      img {
          width: 150px;
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          margin-right: 2rem;
      }

      .product-list-detail {
          flex: 1 1 0;
      }

      .p-rating {
          margin: 0 0 .5rem 0;
      }

      .product-price {
          font-size: 1.5rem;
          font-weight: 600;
          margin-bottom: .5rem;
          align-self: flex-end;
      }

      .product-list-action {
          display: flex;
          flex-direction: column;
      }

      .p-button {
          margin-bottom: .5rem;
      }

  }

  .product-badge {
      border-radius: 2px;
      padding: .25em .5rem;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 12px;
      letter-spacing: .3px;

      &.status-instock {
          background: #C8E6C9;
          color: #256029;
      }

      &.status-outofstock {
          background: #FFCDD2;
          color: #C63737;
      }

      &.status-lowstock {
          background: #FEEDAF;
          color: #8A5340;
      }
  }

  .product-grid-item {
      margin: .5em;
      border: 1px solid #dee2e6;

      .product-grid-item-top,
      .product-grid-item-bottom {
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      img {
          width: 75%;
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          margin: 2rem 0;
      }

      .product-grid-item-content {
          text-align: center;
      }

      .product-price {
          font-size: 1.5rem;
          font-weight: 600;
      }
  }

  @media screen and (max-width: 576px) {
      .product-list-item {
          flex-direction: column;
          align-items: center;

          img {
              width: 75%;
              margin: 2rem 0;
          }

          .product-list-detail {
              text-align: center;
          }

          .product-price {
              align-self: center;
          }

          .product-list-action {
              display: flex;
              flex-direction: column;
          }

          .product-list-action {
              margin-top: 2rem;
              flex-direction: row;
              justify-content: space-between;
              align-items: center;
              width: 100%;
          }
      }
  }
}

