      }

      &.status-lowstock {
          background: #FEEDAF;
          color: #8A5340;
      }
  }

  .order-badge {
      border-radius: 2px;
      padding: .25em .5rem;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 12px;
      letter-spacing: .3px;

      &.order-delivered {
          background: #C8E6C9;
          color: #256029;
      }

      &.order-cancelled {
          background: #FFCDD2;
          color: #C63737;
      }

      &.order-pending {
          background: #FEEDAF;
          color: #8A5340;
      }

      &.order-returned {
