      text-transform: uppercase;
      font-weight: 700;
      font-size: 12px;
      letter-spacing: .3px;

      &.status-instock {
          background: #C8E6C9;
          color: #256029;
      }

      &.status-outofstock {
          background: #FFCDD2;
          color: #C63737;
      }

      &.status-lowstock {
          background: #FEEDAF;
          color: #8A5340;
      }
  }

  /* Responsive */
  .datatable-responsive .p-datatable-tbody > tr > td .p-column-title {
      display: none;
  }

  @media screen and (max-width: 960px) {
      .p-datatable {
          &.datatable-responsive {
              .p-datatable-thead > tr > th,
              .p-datatable-tfoot > tr > td {
                  display: none !important;
              }

              .p-datatable-tbody > tr {
                  border-bottom: 1px solid var(--surface-d);
                  > td {
                      text-align: left;
                      display: flex;
                      align-items: center;
                      border: 0 none !important;
                      width: 100% !important;
                      float: left;
                      clear: left;
